@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Roboto", sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-image: linear-gradient(to bottom, #af006b, #eee7f9);
  background-repeat: no-repeat;
  background-repeat: repeat-x;
  background-size: 100% 150%;
  display: flex;
  flex-wrap: wrap;
}

.lista {
  background-color: #ff9ee7;
  display: flex;
  padding: 15px;
  border-radius: 40px;
  flex-direction: column;
  margin-top: 150px;
}

.lista ul {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.input {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  margin-top: 40px;
}

.input input {
  margin-bottom: 10px;
  border: none;
}

.titulo {
  font-size: 1.5em;
}

.listItem {
  margin-bottom: 5px;
}

.logo {
  height: 20%;
  width: 60%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.image {
  width: 35%;
  height: 25%;
  background-color: #fff;
  border-radius: 50%;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
}

.profileData .name {
  font-size: 4em;
  color: #fff;
}

.profileData p {
  font-size: 1.5em;
  color: #fff;
}

.profileData h1 {
  font-size: 1em;
}

.button {
  background-color: #603285;
  border-style: none;
  margin-top: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  border-radius: 40px;
  color: #fff;
  font-size: 2em;
  padding: 20px;
}

.buttonContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.containerX {
  display: flex;
  flex-direction: column;
}

.input button {
  margin-top: 20px;
}

.input input {
  padding: 20px;
}

.input a {
  color: #603285;
  font-size: 1em;
  margin-top: 10px;
  text-decoration: none;
}

.input {
  margin-bottom: 10px;
}

.register {
  margin-top: 20px;
}

.input button {
  margin-top: 30px;
  margin-bottom: 10px;
}

body {
  background-image: linear-gradient(to bottom, #af006b, #bd5fa1, #ffffff);
  background-repeat: no-repeat;
  background-repeat: repeat-x;
  background-size: 100% 200%;
  display: flex;
  flex-wrap: wrap;
}

.formulario {
  display: flex;
  flex-direction: column;
}

.donie {
  width: 100%;
}

.formulario div input {
  padding: 14px;
  border: none;
  margin-top: -5px;
}

.buttonForm {
  height: 40px;
  width: 200px;
  margin: 30px;
  border: none;
  border-radius: 20px;
  color: #fff;
  background-color: #af006b;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5%;
  flex-wrap: nowrap;
}

